import Link from 'next/link'
import { useEffect, useState } from 'react'

export default function Header() {
  const [path, setPath] = useState<string>('')

  useEffect(() => {
    setPath(window.location.pathname || '/')
  }, [])

  const isActive = (p: string) => path === p

  return (
    <header className="bg-white border-b">
      <div className="w-full px-4 py-3 flex items-center justify-between">
        <div className="text-2xl font-bold text-emerald-900">WattWise</div>
        <nav className="flex gap-4">
          <Link href="/" className={isActive('/') ? 'text-green-600 font-semibold' : 'text-gray-700'}>Home</Link>
          <Link href="/settings" className={isActive('/settings') ? 'text-green-600 font-semibold' : 'text-gray-700'}>Settings</Link>
        </nav>
      </div>
    </header>
  )
}
